import * as React from "react";
import { Tag } from 'antd-mobile'
import { getCourseTag } from "../util"

interface ISprops {
    [propName: string]: any
}
interface IState {
    [propName: string]: any
}
class DetailTitle extends React.Component<ISprops, IState>{
    render() {
        const { course, source } = this.props;
        const code = getCourseTag(course);
        console.log(course)
        return (
            <div className="detail-t">
                <h2>{source ? course.course_name : course.class_title}</h2>
                <div className="detail-t-tip clearfix">
                    {   
                        source?
                        (code || []).map((el, index) => (
                            <Tag key={index} disabled>{el}</Tag>
                        ))
                        :
                        <p>剩余席位: <span>{course.surplus_seat}</span></p>
                    }
                </div>
            </div>
        )
    }
}

export default DetailTitle