//react 基础组件加载
import * as React from "react";
import { Button, Toast } from 'antd-mobile';
import qs from 'qs'

import { getVipDetail } from '../../api/vip'
import { getSeatActiveTime } from '../../util'
import { getUserInfo } from '../../api/bind'
//页面样式加载
import DetailTitle from '../../components/detailTitle';
import CardPanl from '../../components/CardPanl'
import FootAdvertising from '../../components/advertising/footAdvertising'
import '../../assets/less/coursedetail.less';


interface ISprops {
    [propName: string]: any;
}
interface IState {
    courseDetail: any
    courseId: any,
    user_type: any
}


export default class CourseDetail extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            courseDetail: {},
            courseId: '',
            user_type: null
        }
    }
    componentDidMount() {
        document.title = '活动详情';
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        let params = {
            id: data.courseId,
        }
        this.setState({
            courseId: data.courseId,
        })
        getVipDetail(params).then((data) => {
            if (!data.status) {
                this.setState({
                    courseDetail: data.data || []
                })
            }

        })
    }
    handleVip = () => {
        Toast.loading('')
        const { courseId } = this.state;
        getUserInfo({}).then( res => {
            Toast.hide();
            if (!res.status) {
                this.setState({user_type: res.data.user_type},() => {
                    if(this.state.user_type === 2) {
                        this.props.history.push(`/applyOnline?memberType=0&courseId=${courseId}&vip=1&showRelation=1`)
                    } else {
                        this.props.history.push(`/normalMember?courseId=${courseId}&showRelation=1`)
                    }
                })
            } 
        })
        
        
    }
    render() {
        const { courseDetail } = this.state;
        const activeTime = getSeatActiveTime(courseDetail.start_time || '', courseDetail.end_time || '');
        return (
            <div className="course-detail" style={{ position: 'relative'}}>
                <div className="detail-banner">
                    <img src={courseDetail.img_url} alt="" />
                </div>


                <div className="tabs-detail" style={{paddingBottom:'1rem'}}>
                    <DetailTitle course={courseDetail} />
                    <CardPanl title='活动介绍'>
                        <p>{courseDetail.active_content}</p>
                    </CardPanl>
                    <CardPanl title='活动信息'>
                        <p>天数：{courseDetail.days}</p>
                        <p>时间：{activeTime}</p>
                        <p>地点：{courseDetail.province === '' ? courseDetail.city : `${courseDetail.province} - ${courseDetail.city}` }</p>
                    </CardPanl>
                    <FootAdvertising title="vip-detail"/>

                </div>
                <div className="sign-up">
                    <Button type="primary" disabled={courseDetail.surplus_seat === 0} className="sign-up-btn" onClick={this.handleVip}>在线报名</Button>
                </div>

            </div>
        );
    }

}
