import * as React from "react";
import { getAd, getAdInfo } from '../../api/index'

import './advertising.less';
interface ISprops {
    [propName: string]: any,

}
interface IState {
    footList: object[],
}

const FootItem = (props: any) => {
    let el = props.data;
    return(
        <div className="foot-ad-i clearfix" onClick={() => props.self.skip(el)}>
            <div className="left">
                <p>{el.name}</p>
                <span>
                    查看详情
                </span>
            </div>
            <div className="right">
                <img src={el.banner} alt="" />
            </div>
        </div>
    )
    
}
export default class FootAdvertising extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            footList: []
        }
    }
    getAdData(title){
        getAd({
            label: title
        }).then(res => {
            this.setState({
                footList: res.data || []
            })
        })
    }
    skip = (el) => { 
        getAdInfo({
            id: el.id
        }).then(res => {
            window.location.href = el.link;
        })
    }
    componentWillMount() {
        const { title } = this.props;
        this.getAdData(title)
    }   
    render() {
        const { footList } = this.state;
        return (
            <div className="foot-ad">
                {
                    footList && footList.length ? 
                    <React.Fragment>
                        <div className="title clearfix"><span>可能感兴趣的课</span> <strong></strong></div>
                        {
                            footList.map((vipItem, index) => (
                                <FootItem self={this} key={index} data={vipItem} />
                            ))
                        }
                    </React.Fragment>
                    :
                    <div></div>
                }
            </div>
        )
    }

}
